import { Component } from '@angular/core';
import { IonicPage, NavController, AlertController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';


import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  public stSessao: boolean = true;
  public mac;
  formulario: FormGroup;
  //public cpf;
 

  constructor(public navCtrl: NavController,
    public alert: AlertController,
    public formbuilder: FormBuilder,
    public storage: Storage
    ) {
      this.formulario = this.formbuilder.group({
        cpf: [null, [Validators.minLength(10)]]
      })

      this.macCadastrado();
     
  }

  entrar(){
    
    if (this.validaCPF(this.formulario.value.cpf) == true){
      if (this.stSessao == false){
        let alerta = this.alert.create({
          title: "Cadastrar Smartphone",
          message: "É necessário cadastrar o seu smartphone: Seu MAC: 00000:000000:0000",
          inputs: [
            {
              name: 'mac'
            }],
          buttons: [
            {
              text: "Cancelar",
              handler: () => {
                this.navCtrl.push(LoginPage);
              }
            },
            {
              text: "Confirmar",
              handler: data => {
              this.storage.set("mac", data.mac);
                this.navCtrl.setRoot(HomePage);
              }
            }
          ]
        });
        alerta.present()
      } else {
        this.navCtrl.setRoot(HomePage);
      }
    } else {
      let alertaCPF = this.alert.create({
        title: "CPF INVÁLIDO",
        buttons: [
          {
            text: "Fechar",
            handler: () => {
              this.navCtrl.push(LoginPage);
            }
          }
        ]
      });
      alertaCPF.present()
    }
  }
    
   


  macCadastrado(){
    this.storage.get('mac')
      .then(res => {
        if (res) {
         
          // this.navCtrl.setRoot(HomePage, {});
          this.stSessao = true;
          // this.mac = res;
         
        } else {
          //return false;
          this.stSessao = false;
        }
      })
  }

  validaCPF(cpf: any)
  {
    var numeros, digitos, soma, i, resultado, digitos_iguais;
    digitos_iguais = 1;
    if (cpf.length < 11)
          return false;
    for (i = 0; i < cpf.length - 1; i++)
          if (cpf.charAt(i) != cpf.charAt(i + 1))
                {
                digitos_iguais = 0;
                break;
                }
    if (!digitos_iguais)
          {
          numeros = cpf.substring(0,9);
          digitos = cpf.substring(9);
          soma = 0;
          for (i = 10; i > 1; i--)
                soma += numeros.charAt(10 - i) * i;
          resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
          if (resultado != digitos.charAt(0))
                return false;
          numeros = cpf.substring(0,10);
          soma = 0;
          for (i = 11; i > 1; i--)
                soma += numeros.charAt(11 - i) * i;
          resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
          if (resultado != digitos.charAt(1))
                return false;
          return true;
          }
    else
        return false;
  }
}



